import AllCharities from "@/views/charities/AllCharities.vue";
import EditCharity from "@/views/charities/EditCharity.vue";
import ShowCharity from "@/views/charities/ShowCharity.vue";
import CreateCharity from "@/views/charities/CreateCharity.vue";

export const charityRoutes = [
  { path: "/charities", name: "all-charities", component: AllCharities },
  { path: "/charities/create", name: "create-charity", component: CreateCharity },
  { path: "/charities/:id", name: "show-charity", component: ShowCharity, props: true },
  { path: "/charities/:id/edit", name: "edit-charity", component: EditCharity, props: true },
];
