import { apiClient } from "./Service";

export default {
  async getAllUsers(token, page) {
    return await apiClient.get(`/users?page=${page}&limit=10`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async getAllUsersByFilter(token, data) {
    return await apiClient("/users/filter?page=1&limit=10", {
      headers: {
        authorization: `Bearer ${token}`,
      },
      method: "POST",
      data: data,
    });
  },

  async getUserById(token, id) {
    return await apiClient.get(`/users/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async getUserFullData(token, id) {
    return await apiClient.get(`users/get/full_data/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async updateUser(token, id, data) {
    return await apiClient(`/admins/user/${id}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      method: "PATCH",
      data: data,
    });
  },

  /**
   * @param data: {mobile, password}
   */
  async createUser(token, data) {
    return await apiClient("/auth/user/signup", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      method: "POST",
      data: data,
    });
  },
};
