import { apiClient } from "./Service";

export default {
  async getAllTalents(token, page) {
    return await apiClient.get(`/talents?page=${page}&limit=10`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async getAllTalentsByFilter(token, data) {
    return await apiClient("/talents/filter?page=1&limit=10", {
      headers: {
        authorization: `Bearer ${token}`,
      },
      method: "POST",
      data: data,
    });
  },

  async getTalentById(token, id) {
    return await apiClient.get(`/talents/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async getTalentFullData(token, id) {
    return await apiClient.get(`talents/get/full_data/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async updateTalent(token, id, data) {
    return await apiClient(`/admins/talent/${id}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      method: "PATCH",
      data: data,
    });
  },

  /**
   * @param data: {mobile, name, password}
   */
  async createTalent(token, data) {
    return await apiClient("/auth/talent/signup", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      method: "POST",
      data: data,
    });
  },
};
