<template lang="pug">
BodyComponent
  #main
    .row
      C-Breadcrump(
        title="ایجاد کاربر",
        start="خانه",
        :startPath="{ name: 'home-page' }",
        middle="کاربرها",
        :middlePath="{ name: 'all-users' }",
        last="ایجاد کاربر"
      )
      .col.s12
        .container
          // users edit start
          .section.users-edit
            .card
              .card-content
                .row
                  #account.col.s12.active
                    // users edit account form start
                    form#accountForm(novalidate="novalidate", @submit.prevent="createUser")
                      .row
                        // users edit media object start
                        .media.display-flex.align-items-center.mb-2
                          a.mr-2
                            img.z-depth-4.circle(
                              src="/app-assets/images/avatar/guest.png",
                              alt="users avatar",
                              height="150",
                              width="150"
                            )
                          .media-body
                            h5.media-heading.mt-0 ایجاد کاربر جدید
                            .user-edit-btns.display-flex
                              button.btn-small.indigo(type="submit") تغییر دادن
                              a.btn-small.btn-light-pink تنظیم مجدد
                        // users edit media object ends
                        .col.s12.m6
                          .row
                            .col.s12.input-field
                              input#mobile.validate(name="mobile", type="text", data-error=".errorTxt2")
                              label.active(for="mobile") موبایل
                              small.errorTxt2

                        .col.s12.m6
                          .row
                            .col.s12.input-field
                              input#password.validate(name="password", type="password", data-error=".errorTxt3")
                              label.active(for="password") رمز عبور
                              small.errorTxt3

                        .col.s12.display-flex.justify-content-end.mt-3
                          button.btn.indigo.iransans(type="submit")
                            | ذخیره تغییرات
                          button.btn.btn-light.iransans(type="button", @click="this.$router.go(-1)") لغو
                    // users edit account form ends
          // users edit ends
</template>

<script>
import { Toast } from "@/common/plugins/sweetalert";
import VueCookies from "vue-cookies";
import BodyComponent from "@/layouts/BodyComponent";
import UserService from "@/services/UserService.js";
export default {
  components: {
    BodyComponent,
  },
  data() {
    return {
      token: "",
      formData: {},
    };
  },
  methods: {
    // call needed function to update
    createUser(submitEvent) {
      this.getFormData(submitEvent);
      UserService.createUser(this.token, this.formData)
        .then(response => {
          Toast("success", "کاربر به درستی ایجاد شد.");
          this.$router.push({ name: "edit-user", params: { id: response.data.id } });
        })
        .catch(error => {
          Toast("error", "مشکلی در ایجاد کاربر رخ داد.");
          console.log(`there was an error: ${error}`);
        });
    },
    // fill or update form data
    getFormData(submitEvent) {
      const data = submitEvent.target.elements;
      this.formData = {
        mobile: data.mobile.value,
        password: data.password.value,
      };
      if (!this.formData.password.trim()) {
        delete this.formData.password;
      }
    },
  },
  created() {
    this.token = VueCookies.get("adminToken");
  },
};
</script>

<style lang="scss" scoped>
@import "/app-assets/vendors/select2/select2.min.css";
@import "/app-assets/vendors/select2/select2-materialize.css";
</style>
