<template lang="pug">
li(:class="{ active: isActive }")
  router-link(:to="link", :class="{ active: isActive }")
    i.material-icons {{ icon }}
    span(:data-i18n="translateKey") {{ itemName }}
</template>

<script>
export default {
  props: {
    itemName: { type: String, default: "Item Name Not Found" },
    link: { type: [String, Object] },
    isActive: { type: Boolean, default: false },
    translateKey: { type: String },
    icon: { type: String, default: "radio_button_unchecked" },
  },
};
</script>
