<template lang="pug">
BodyComponent
  #main
    .row
      C-Breadcrump(
        :title="breadcrumpTitle",
        start="خانه",
        :startPath="{ name: 'home-page' }",
        middle="کاربرها",
        :middlePath="{ name: 'all-talents' }",
        :last="person.name"
      )
      .col.s12
        .container
          // users view start
          .section.users-view
            // users view media object start
            .card-panel
              .row
                .col.s12.m7
                  .display-flex.media
                    a.avatar(href="#")
                      img.z-depth-4.circle(
                        :src="[person.image ? person.image : '/app-assets/images/avatar/guest.png']",
                        alt="users view avatar",
                        height="64",
                        width="64"
                      )
                    .media-body
                      h6.media-heading
                        span.users-view-name {{ person.name }}
                        span.grey-text.ml-3.mr-3 @
                        span.users-view-username.grey-text {{ person.mobile }}
                      span شناسه:
                      span.en.users-view-id.ml-3 {{ person.id }}
                .col.s12.m5.quick-action-btns.display-flex.justify-content-end.align-items-center.pt-2
                  router-link.btn-small.indigo(:to="{ name: 'edit-talent', props: { id: person.id } }") ویرایش
            // users view media object ends
            // users view card data start
            .card
              .card-content
                .row
                  .col.s12.m4
                    table.striped
                      tbody
                        tr
                          td ثبت شده است :
                          td {{ person.created_at }}
                        tr
                          td آخرین بروزرسانی :
                          td.users-view-latest-activity {{ person.updated_at }}
                        tr
                          td تأیید شده :
                          td
                            span.users-view-status.chip.lighten-5(
                              :class="person.isApproved ? 'green green-text' : 'red red-text'"
                            ) {{ person.isApproved ? 'بله' : 'خیر' }}
                        tr
                          td فعال شده :
                          td
                            span.users-view-status.chip.lighten-5(
                              :class="person.isActive ? 'green green-text' : 'red red-text'"
                            ) {{ person.isActive ? 'بله' : 'خیر' }}
                        tr
                          td بلاک شده :
                          td
                            span.users-view-status.chip.lighten-5(
                              :class="person.isBlocked ? 'red red-text' : 'green green-text'"
                            ) {{ person.isBlocked ? 'بله' : 'خیر' }}
                  .col.s12.m8
                    table.responsive-table
                      thead
                        tr
                          th مجوز نمونه
                          th خواندن
                          th نوشتن
                          th ساختن
                          th حذف
                      tbody
                        tr
                          td کاربران
                          td بله
                          td خیر
                          td خیر
                          td بله
                        tr
                          td مقالات
                          td خیر
                          td بله
                          td خیر
                          td بله
                        tr
                          td کارمند
                          td بله
                          td بله
                          td خیر
                          td خیر
            // users view card data ends
            // users view card details start
            .card
              .card-content
                .row.indigo.lighten-5.border-radius-4.mb-2
                  .col.s12.m3.users-view-timeline
                    h6.indigo-text.m-0
                      | درخواست‌ ویدیو :
                      span.ml-2 {{ userBookRequests }}
                  .col.s12.m3.users-view-timeline
                    h6.indigo-text.m-0
                      | دسته‌بندی‌ها :
                      span.ml-2 {{ userIntrestedCategories }}
                  .col.s12.m3.users-view-timeline
                    h6.indigo-text.m-0
                      | نظرات :
                      span.ml-2 {{ userReviewsCount }}
                  .col.s12.m3.users-view-timeline
                    h6.indigo-text.m-0
                      | مورد علاقه‌ها :
                      span.ml-2 {{ userWishlist }}
                  .col.s12.m3.users-view-timeline
                    h6.indigo-text.m-0
                      | پست‌های این کاربر :
                      span.ml-2 {{ userPosts }}
                  .col.s12.m3.users-view-timeline
                    h6.indigo-text.m-0
                      | پست‌های ذخیره شده :
                      span.ml-2 {{ userSavedPosts }}
                  .col.s12.m3.users-view-timeline
                    h6.indigo-text.m-0
                      | پست‌های پسندیده شده :
                      span.ml-2 {{ userLikedPosts }}
                  .col.s12.m3.users-view-timeline
                    h6.indigo-text.m-0
                      | پیام‌ها :
                      span.ml-2 {{ userNotifications }}
                .row
                  //- .col.s12
                  .col.s12.m6
                    h6.mb-2.mt-2
                      i.material-icons error_outline
                      |
                      | اطلاعات شخصی
                    table.striped
                      tbody
                        tr
                          td موبایل :
                          td.users-view-username {{ person.mobile }}
                        tr
                          td نام :
                          td.users-view-name {{ person.name }}
                        tr
                          td ایمیل :
                          td.users-view-email {{ person.email }}
                    h6.mb-2.mt-2
                      i.material-icons link
                      |
                      | پیوندهای اجتماعی
                    table.striped
                      tbody
                        tr
                          td توویتر :
                          td
                            a(href="#") https://www.twitter.com/
                        tr
                          td فیس بوک :
                          td
                            a(href="#") https://www.facebook.com/
                        tr
                          td اینستاگرام :
                          td
                            a(href="#") https://www.instagram.com/
                  .col.s12.m6
                    h6.mb-2.mt-2
                      i.material-icons add_shopping_cart
                      |
                      | اطلاعات تجاری
                    table.striped
                      tbody
                        tr
                          td دارایی :
                          td {{ numberToCash(person.cash) }} تومان
                        tr
                          td دارایی قفل شده :
                          td {{ numberToCash(person.blocked_cash) }} تومان
                        tr
                          td قیمت ویدیو :
                          td {{ numberToCash(person.book_price) }} تومان
                        tr
                          td زمان پاسخگویی :
                          td {{ person.response_time }}
                        tr
                          td توضیحات کوتاه :
                          td {{ person.short_description }}
                        tr
                          td توضیحات کامل :
                          td {{ person.description }}
                        tr
                          td دسته‌بندی انتخابی :
                          td(v-if="person.intrested_categories && person.intrested_categories.length")
                            p(v-for="intrested_category in person.intrested_categories") {{ intrested_category.title }}
                          td(v-else) خالی
                        tr
                          td دسته‌بند اصلی :
                          td(v-if="person.working_sub_category && person.working_sub_category.length")
                            p(v-for="working_sub_category in person.working_sub_category") {{ working_sub_category.title }}
                          td(v-else) خالی
            // users view card details ends
</template>

<script>
import VueCookies from "vue-cookies";
import BodyComponent from "@/layouts/BodyComponent";
import UserService from "@/services/UserService.js";
export default {
  props: {
    id: { type: String },
  },
  components: {
    BodyComponent,
  },
  data() {
    return {
      token: "",
      person: {},
      breadcrumpTitle: "",
    };
  },
  methods: {
    // get data by id
    getUserData() {
      this.token = VueCookies.get("adminToken");
      UserService.getUserFullData(this.token, this.id)
        .then(response => {
          this.person = response.data;
          this.breadcrumpTitle = `نمایش پروفایل ${this.person.name}`;
        })
        .catch(() => this.$router.go(-1));
    },
    numberToCash(number) {
      const cash = new Number(number).toLocaleString("fa-IR");
      return cash !== "ناعدد" ? cash : 0;
    },
  },
  computed: {
    userBookRequests() {
      return this.person.book_requests && this.person.book_requests.length ? this.person.book_requests.length : 0;
    },
    userIntrestedCategories() {
      return this.person.intrested_categories && this.person.intrested_categories.length
        ? this.person.intrested_categories.length
        : 0;
    },
    userWishlist() {
      return this.person.wishlist && this.person.wishlist.length ? this.person.wishlist.length : 0;
    },
    userReviewsCount() {
      return this.person.reviews && this.person.reviews.length ? this.person.reviews.length : 0;
    },
    userPosts() {
      return this.person.posts && this.person.posts.length ? this.person.posts.length : 0;
    },
    userSavedPosts() {
      return this.person.saved_posts && this.person.saved_posts.length ? this.person.saved_posts.length : 0;
    },
    userLikedPosts() {
      return this.person.liked_posts && this.person.liked_posts.length ? this.person.saved_posts.length : 0;
    },
    userNotifications() {
      return this.person.notifications && this.person.notifications.length ? this.person.notifications.length : 0;
    },
  },
  created() {
    this.getUserData();
  },
};
</script>

<style lang="scss" scoped>
@import "/app-assets/vendors/select2/select2.min.css";
@import "/app-assets/vendors/select2/select2-materialize.css";
</style>
