<template lang="pug">
router-view
</template>

<script>
import { BodyComponentScripts } from "@/common/global/scripts";
import { addStaticScripts } from "@/common/utils/addStatics";
import VueCookies from "vue-cookies";
export default {
  beforeCreate() {
    if (!VueCookies.get("adminToken")) this.$router.push("/login");
  },
  mounted() {
    addStaticScripts(BodyComponentScripts);
  },
};
</script>
