<template lang="pug">
#breadcrumbs-wrapper.pt-1.pb-0
  // Search for small screen
  .container
    .row
      .col.s12.m6.l6
        h5.breadcrumbs-title
          span {{ title }}
      .col.s12.m6.l6.right-align-md
        ol.breadcrumbs.mb-0
          li.breadcrumb-item(v-if="startPath")
            router-link(:to="startPath") {{ start }}
          li.breadcrumb-item(v-if="middlePath")
            router-link(:to="middlePath", active-class="", exact-active-class="") {{ middle }}
          li.breadcrumb-item.active(v-if="last")
            | {{ last }}
</template>

<script>
export default {
  props: {
    title: { type: String },
    start: { type: String, default: "خانه" },
    startPath: { type: [Object, String], default: "/" },
    middle: { type: String },
    middlePath: { type: [Object, String] },
    last: { type: String },
  },
};
</script>
