import { apiClient } from "./Service";

export default {
  async getAllAdmins(token, page) {
    return await apiClient.get(`/admins?page=${page}&limit=10`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async getAllAdminsByFilter(token, data) {
    return await apiClient("/admins/filter?page=1&limit=10", {
      headers: {
        authorization: `Bearer ${token}`,
      },
      method: "POST",
      data: data,
    });
  },

  async getAdminById(token, id) {
    return await apiClient.get(`/admins/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async getAdminFullData(token, id) {
    return await apiClient.get(`admins/get/full_data/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async updateAdmin(token, id, data) {
    return await apiClient(`/admins/${id}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      method: "PATCH",
      data: data,
    });
  },

  /**
   * @param data: {mobile, name, password}
   */
  async createAdmin(token, data) {
    return await apiClient("/auth/admin/signup", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      method: "POST",
      data: data,
    });
  },
};
