import { apiClient } from "./Service";

export default {
  async getAllCharities(token, page) {
    return await apiClient.get(`/charities?page=${page}&limit=10`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async getAllCharitiesByFilter(token, data) {
    return await apiClient("/charities/filter?page=1&limit=10", {
      headers: {
        authorization: `Bearer ${token}`,
      },
      method: "POST",
      data: data,
    });
  },

  async getCharityById(token, id) {
    return await apiClient.get(`/charities/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async getCharityFullData(token, id) {
    return await apiClient.get(`charities/get/full_data/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async updateCharity(token, id, data) {
    return await apiClient(`/admins/charity/${id}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      method: "PATCH",
      data: data,
    });
  },

  /**
   * @param data: {mobile, password}
   */
  async createCharity(token, data) {
    return await apiClient("/auth/charity/signup", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      method: "POST",
      data: data,
    });
  },
};
