<template lang="pug">
.row
  .col.s12
    .container
      #login-page.row
        .col.s12.m6.l4.z-depth-4.card-panel.border-radius-6.login-card.bg-opacity-8
          form.login-form(@submit.prevent="login")
            .row
              .input-field.col.s12
                h5.ml-4 ورود
            .row.margin
              .input-field.col.s12
                i.material-icons.prefix.pt-2 person_outline
                input#mobile(type="text", v-model="mobile")
                label.center-align(for="mobile") موبایل
            .row.margin
              .input-field.col.s12
                i.material-icons.prefix.pt-2 lock_outline
                input#password(type="password", v-model="password")
                label(for="password") رمز عبور
            .row
              .input-field.col.s12
                a.btn.waves-effect.waves-light.border-round.gradient-45deg-purple-deep-orange.col.s12(@click="login") وارد شدن
            .row
              .input-field.col.s6.m6.l6
                p.margin.medium-small
                  a(href="user-register.html") اکنون ثبت نام کنید
              .input-field.col.s6.m6.l6
                p.margin.right-align.medium-small
                  a(href="user-forgot-password.html") رمز عبور را فراموش کرده اید؟
    .content-overlay
</template>

<script>
import { Toast } from "@/common/plugins/sweetalert";
import AuthService from "@/services/AuthService.js";
import VueCookies from "vue-cookies";
export default {
  data() {
    return {
      mobile: "",
      password: "",
    };
  },
  methods: {
    login() {
      const authData = JSON.stringify({ mobile: this.mobile, password: this.password });
      AuthService.login(authData)
        .then(response => {
          VueCookies.set("adminToken", response.data.accessToken, "240h", "/");
          this.$router.push("/");
        })
        .catch(() => Toast("error", "مشکلی در لاگین رخ داد."));
    },
  },
  beforeCreate() {
    if (VueCookies.get("adminToken")) this.$router.push("/");
  },
  mounted() {
    const script = document.createElement("script");
    script.async = true;
    script.setAttribute("type", "text/javascript");
    script.textContent =
      'document.getElementsByTagName("body")[0].style = "background-image: url(/app-assets/images/gallery/flat-bg.jpg); background-repeat: no-repeat; background-size: cover;"';
    document.body.appendChild(script);
  },
  beforeUnmount() {
    document.getElementsByTagName("body")[0].style = "";
  },
};
</script>

<style lang="scss" scoped>
@import "/app-assets/css-rtl/pages/login.css";
</style>
