<template lang="pug">
.responsive-table
  #users-list-datatable_wrapper.dataTables_wrapper.no-footer
    table#users-list-datatable.table.dataTable.no-footer.dtr-inline(
      role="grid",
      aria-describedby="users-list-datatable_info",
      style="width: 1560px"
    )
      thead
        tr(role="row")
          th.sorting_asc(rowspan="1", colspan="1", style="width: 32px", aria-label="")
          th(v-if="id") شناسه کاربری
          th(
            v-for="(header, headerKey) in headers",
            :key="headerKey",
            tabindex="0",
            aria-controls="users-list-datatable",
            rowspan="1",
            colspan="1",
            style="width: 112px",
            aria-label=`${header}: activate to sort column ascending`
          ) {{ header }}
      tbody
        tr(role="row", v-for="(data, index) in AllData.items", :class="[index % 2 ? 'even' : 'odd']", :key="index")
          td.sorting_1(tabindex="0")
          td(v-if="id") {{ data.id }}

          td(v-if="data.parameter && data.parameter.notifyParams && data.parameter.notifyParams.icon")
            img.circle(
              :src="globals.baseURL + '/' + data.parameter.notifyParams.icon",
              alt="users avatar",
              height="40",
              width="40"
            )
          td(v-else)
            img.circle(
              src="/app-assets/images/icon/default-notification.png",
              alt="users avatar",
              height="40",
              width="40"
            )

          td(v-if="data.parameter && data.parameter.notifyParams && data.parameter.notifyParams.template") {{ data.parameter.notifyParams.template }}

          td.cursor-pointer.select-none(v-if="data.isWatched", @click="toggleIsWatched(data.id)")
            span.chip.red.lighten-5
              span.red-text خوانده شده
          td.cursor-pointer.select-none(v-else, @click="toggleIsWatched(data.id)")
            span.chip.green.lighten-5
              span.green-text جدید

          td {{ data.notification_target }}
          td {{ data.notification_type }}

    #users-list-datatable_info.dataTables_info(role="status", aria-live="polite") نمایش {{ pagination.currentPage * pagination.itemsPerPage - pagination.itemsPerPage + 1 }} تا {{ pagination.currentPage * pagination.itemsPerPage }} از {{ pagination.totalItems }} داده
    #users-list-datatable_paginate.dataTables_paginate.paging_simple_numbers
      a#users-list-datatable_previous.paginate_button.previous.disabled(
        aria-controls="users-list-datatable",
        data-dt-idx="0",
        tabindex="-1",
        @click="firstPage"
      ) اولین
      span
        a.paginate_button(
          v-for="(page, pageIndex) in pagination.totalPages",
          :class="{ current: pagination.currentPage - 1 === pageIndex }",
          v-show="pagination.currentPage > pageIndex - 5",
          :key="pageIndex",
          aria-controls="users-list-datatable",
          data-dt-idx="1",
          tabindex="0",
          @click="callPage"
        ) {{ pageIndex + 1 }}
      a#users-list-datatable_next.paginate_button.next(
        aria-controls="users-list-datatable",
        data-dt-idx="5",
        tabindex="0",
        @click="lastPage"
      ) آخرین
</template>

<script>
import { ToEnlishNumber } from "@/common/utils/helpers";
import { globals } from "@/common/global/globals";
export default {
  props: {
    id: { type: Boolean, default: false },
    AllData: { type: [Array, Object] },
    headers: { type: Array },
    pagination: { type: Object }, // contain { currentPage, itemCount, itemsPerPage, totalItems, totalPages }
  },
  data() {
    return {
      limit: 10,
      globals,
      page: 1,
    };
  },
  methods: {
    toggleIsWatched(id) {
      this.$emit("toggle-watched", id, this.page);
    },
    firstPage() {
      this.$emit("call-page", 1);
    },
    callPage(e) {
      this.page = ToEnlishNumber(e.path[0].innerText);
      this.$emit("call-page", this.page);
    },
    lastPage() {
      this.$emit("call-page", this.pagination.totalPages);
    },
  },
};
</script>

<style lang="scss" scoped>
.cursor-pointer {
  cursor: pointer;
}
.select-none {
  outline: none;
  user-select: none;
}
table {
  width: -webkit-fill-available !important;
  width: fill-available !important;
  overflow-x: auto;
}
</style>
