import AllUsers from "@/views/users/AllUsers.vue";
import EditUser from "@/views/users/EditUser.vue";
import ShowUser from "@/views/users/ShowUser.vue";
import CreateUser from "@/views/users/CreateUser.vue";

export const userRoutes = [
  { path: "/users", name: "all-users", component: AllUsers },
  { path: "/users/create", name: "create-user", component: CreateUser },
  { path: "/users/:id", name: "show-user", component: ShowUser, props: true },
  { path: "/users/:id/edit", name: "edit-user", component: EditUser, props: true },
];
