<template lang="pug">
BodyComponent
  #main
    .row
      C-Breadcrump(
        title="نوتیفیکیشن‌های شما"
        start="خانه"
        :startPath="{ name: 'home-page' }"
        middle="نوتیفیکیشن‌ها"
        :middlePath="{ name: 'your-notifications' }"
        last="نوتیفیکیشن‌های شما"
      )
      .col.s12
        .container
          section.users-list-wrapper.section

            // notifications list filter
            .users-list-filter(v-if="filterFormReady")
              .card-panel
                .row
                  form(@submit.prevent="submitFilter")
                    .col.s12.m6.l3(v-for="filterData in filterDatas")
                      label(for="users-list-verified") {{ filterData.title }}
                      .input-field
                        .select-wrapper
                          select.form-control(:name="filterData.name" :id="filterData.name" tabindex="-1")
                            option(v-for="option of filterData.options" :value="option.value") {{ option.title }}
                    .col.s12.m6.l3.display-flex.align-items-center.show-btn.float-left
                      button.btn.btn-block.indigo.waves-effect.waves-light(type="submit") فیلتر

            .users-list-table
              .card
                .card-content
                  // datatable start
                  N-Table(
                    :AllData="notifications"
                    :headers=[ "تصویر", "تیتر", "وضعیت", "هدف", "نوع", ],
                    :pagination="pagination",
                    @call-page="callPage",
                    @toggle-watched="toggleIsWatched"
                  )
                  // datatable ends
          // users list ends
</template>

<script>
import VueCookies from "vue-cookies";
import { Toast } from "@/common/plugins/sweetalert";
import { TableScripts } from "@/common/global/scripts";
import { addStaticScripts } from "@/common/utils/addStatics";
import NotificationService from "@/services/NotificationService.js";
import BodyComponent from "@/layouts/BodyComponent";
export default {
  components: {
    BodyComponent,
  },
  data() {
    return {
      notifications: {},
      token: "",
      notification_types: [],
      notification_targets: [],
      filterDatas: [],
      formData: {},
      pagination: { currentPage: 1 },
      filterFormReady: false,
    };
  },
  methods: {
    // call needed function to apply filter
    submitFilter(submitEvent) {
      this.getFormData(submitEvent);
      this.getNotificationWithFilter();
    },
    // fill or update form data
    getFormData(submitEvent) {
      const data = submitEvent.target.elements;
      this.formData = {
        isWatched: JSON.parse(data.isWatched.value),
        type: data.type.value,
        target: data.target.value,
      };
    },
    getNotificationWithFilter() {
      NotificationService.getAllPersonNotifications(this.token, this.pagination.currentPage, this.formData)
        .then(response => (this.notifications = response.data))
        .catch(error => console.log(`there was an error: ${error}`));
    },
    callPage(page) {
      this.token = VueCookies.get("adminToken");
      NotificationService.getAllPersonNotifications(this.token, page)
        .then(response => {
          this.notifications = response.data;
          this.pagination = response.data.meta;
          for (const notification of this.notifications.items) {
            this.notification_types.push({
              value: notification.notification_type,
              title: notification.notification_type,
            });
            this.notification_targets.push({
              value: notification.notification_target,
              title: notification.notification_target,
            });
            this.filterDatas = [
              {
                name: "isWatched",
                title: "وضعیت",
                options: [
                  { value: "false", title: "خیر" },
                  { value: "true", title: "بله" },
                ],
              },
              {
                name: "type",
                title: "نوع",
                options: this.notification_types,
              },
              {
                name: "target",
                title: "هدف",
                options: this.notification_targets,
              },
            ];
            this.filterFormReady = true;
          }
        })
        .catch(error => {
          Toast("error", "مشکلی در دریافت اطلاعات لیست رخ داد.");
          console.log(`there was an error: ${error}`);
        });
    },
    toggleIsWatched(id, page) {
      NotificationService.toggleNotificationIsWatched(this.token, id)
        .then(response => {
          console.log(response.data);
          this.callPage(page);
        })
        .catch(error => {
          Toast("error", "مشکلی در تاگل کردن نوتیفیکیشن رخ داد.");
          console.log(`there was an toggle error: ${error}`);
        });
    },
  },
  created() {
    this.callPage(this.pagination.currentPage);
  },
  mounted() {
    addStaticScripts(TableScripts);
  },
};
</script>

<style lang="scss" scoped>
@import "/app-assets/vendors/data-tables/css/jquery.dataTables.min.css";
@import "/app-assets/vendors/data-tables/extensions/responsive/css/responsive.dataTables.min.css";
@import "/app-assets/css-rtl/pages/page-users.min.css";
.float-left {
  float: left !important;
}
</style>
