import { createRouter, createWebHistory } from "vue-router";
import Home from "@/views/home/Home.vue";
import { authRoutes } from "./compact/auths.js";
import { talentRoutes } from "./compact/talents.js";
import { userRoutes } from "./compact/users.js";
import { charityRoutes } from "./compact/charities.js";
import { adminRoutes } from "./compact/admins.js";
import { notificationRoutes } from "./compact/notification.js";
import { otherRoutes } from "./compact/others.js";

const routes = [
  { path: "/", name: "home-page", component: Home },
  ...authRoutes,
  ...talentRoutes,
  ...userRoutes,
  ...charityRoutes,
  ...adminRoutes,
  ...notificationRoutes,
  ...otherRoutes,
];

const router = createRouter({
  history: createWebHistory(),
  routes,
  linkExactActiveClass: "active", // active class for *exact* links.
  linkActiveClass: "active", // active class for non-exact links.
});

export default router;
