<template lang="pug">
// BEGIN: SideNav
aside.sidenav-main.nav-expanded.nav-lock.nav-collapsible.sidenav-light.sidenav-active-square
  .brand-sidebar
    h1.logo-wrapper
      router-link.brand-logo.darken-1(:to="{ name: 'home-page' }")
        img.hide-on-med-and-down(src="/app-assets/images/logo/materialize-logo-color.png", alt="materialize logo")
        img.show-on-medium-and-down.hide-on-med-and-up(
          src="/app-assets/images/logo/materialize-logo.png",
          alt="materialize logo"
        )
        span.logo-text.hide-on-med-and-down {{ AppName }}
      a.navbar-toggler(href="#")
        i.material-icons radio_button_checked
  ul#slide-out.sidenav.sidenav-collapsible.leftside-navigation.collapsible.sidenav-fixed.menu-shadow(
    data-menu="menu-navigation",
    data-collapsible="menu-accordion"
  )
    <S-DropDown title="داشبورد" translateKey="Dashboard" icon="dashboard" notifCount="3">
      <S-DropDown-Item itemName="خانه" :link="{ name: 'home-page' }" translateKey="Modern" />
    </S-DropDown>

    <S-Devider title="تلنت‌ها" />
    <S-DropDown title="امور مرتبط با تلنت" translateKey="Talent Menu" icon="star_rate">
      <S-DropDown-Item itemName="همه تلنت‌ها" :link="{ name: 'all-talents' }" translateKey="Modern Menu" />
      <S-DropDown-Item itemName="ایجاد تلنت" :link="{ name: 'create-talent' }" translateKey="Modern Menu" />
    </S-DropDown>

    <S-Devider title="کاربرها" />
    <S-DropDown title="امور مرتبط با کاربرها" translateKey="User Menu" icon="account_circle">
      <S-DropDown-Item itemName="همه کاربرها" :link="{ name: 'all-users' }" translateKey="Modern Menu" />
      <S-DropDown-Item itemName="ایجاد کاربر" :link="{ name: 'create-user' }" translateKey="Modern Menu" />
    </S-DropDown>

    <S-Devider title="خیریه‌ها" />
    <S-DropDown title="امور مرتبط با خیریه‌ها" translateKey="Charity Menu" icon="cake">
      <S-DropDown-Item itemName="همه خیریه‌ها" :link="{ name: 'all-charities' }" translateKey="Modern Menu" />
      <S-DropDown-Item itemName="ایجاد خیریه" :link="{ name: 'create-charity' }" translateKey="Modern Menu" />
    </S-DropDown>

    <S-Devider title="ادمین‌ها" />
    <S-DropDown title="ادمین‌ها" translateKey="Admin Menu" icon="fingerprint">
      <S-DropDown-Item itemName="همه ادمین‌ها" :link="{ name: 'all-admins' }" translateKey="Modern Menu" />
      <S-DropDown-Item itemName="ایجاد ادمین" :link="{ name: 'create-admin' }" translateKey="Modern Menu" />
    </S-DropDown>

    <S-Devider title="نوتیفیکیشن‌ها" />
    <S-DropDown title="نوتیفیکیشن‌ها" translateKey="Notification Menu" icon="notifications_active">
      <S-DropDown-Item itemName="نوتیفیکیشن‌های شما" :link="{ name: 'your-notifications' }" translateKey="Modern Menu" />
    </S-DropDown>

    li(:class="{ active: isActive }")
      a(@click="logout")
        i.material-icons exit_to_app
        span(data-i18n="logout") خروج

  .navigation-background
  a.sidenav-trigger.btn-sidenav-toggle.btn-floating.btn-medium.waves-effect.waves-light.hide-on-large-only(
    href="#",
    data-target="slide-out"
  )
    i.material-icons menu
// END: SideNav
</template>

<script>
import VueCookies from "vue-cookies";
export default {
  props: {
    AppName: { type: String },
  },
  methods: {
    logout() {
      VueCookies.remove("adminToken");
      this.$router.go("{ name: 'login-page' }");
    },
  },
};
</script>
