import { apiClient } from "./Service";

export default {
  async getAllPersonNotifications(token, page, data) {
    let uri = `/notifications/get/person/notifications?page=${page}&limit=10`;

    if (data && data.type) uri += `&type=${data.type}`;
    if (data && data.target) uri += `&target=${data.target}`;
    if (data && data.isWatched) uri += `&isWatched=${data.isWatched}`;
    console.log(uri);
    return await apiClient.get(uri, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async toggleNotificationIsWatched(token, id) {
    return await apiClient(`/notifications/toggle/watched/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
      method: "PATCH",
    });
  },

  /* -------------------------------------------------------------------------- */

  async getAllAdminsByFilter(token, data) {
    return await apiClient("/admins/filter?page=1&limit=10", {
      headers: {
        authorization: `Bearer ${token}`,
      },
      method: "POST",
      data: data,
    });
  },

  async getAdminById(token, id) {
    return await apiClient.get(`/admins/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async getAdminFullData(token, id) {
    return await apiClient.get(`admins/get/full_data/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async updateAdmin(token, id, data) {
    return await apiClient(`/admins/${id}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      method: "PATCH",
      data: data,
    });
  },

  /**
   * @param data: {mobile, name, password}
   */
  async createAdmin(token, data) {
    return await apiClient("/auth/admin/signup", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      method: "POST",
      data: data,
    });
  },
};
