<template lang="pug">
BodyComponent
  #main
    .row
      C-Breadcrump(
        title="لیست تلنت‌ها"
        start="خانه"
        :startPath="{ name: 'home-page' }"
        middle="تلنت‌ها"
        :middlePath="{ name: 'all-talents' }"
        last="لیست تلنت‌ها"
      )
      .col.s12
        .container
          section.users-list-wrapper.section

            // users list filter
            .users-list-filter
              .card-panel
                .row
                  form(@submit.prevent="submitFilter")
                    .col.s12.m6.l3(v-for="filterData in filterDatas")
                      label(for="users-list-verified") {{ filterData.title }}
                      .input-field
                        .select-wrapper
                          select.form-control(:name="filterData.name" :id="filterData.name" tabindex="-1")
                            option(v-for="option in filterData.options" :value="option.value") {{ option.title }}
                    .col.s12.m6.l3.display-flex.align-items-center.show-btn
                      button.btn.btn-block.indigo.waves-effect.waves-light(type="submit") فیلتر

            .users-list-table
              .card
                .card-content
                  // datatable start
                  P-Table(
                    :AllData="persons",
                    :headers=[ "نام", "موبایل", "محبوبیت", "بلاک", "فعال", "تأیید" ],
                    editComponent="edit-talent",
                    showComponent="show-talent",
                    :pagination="pagination",
                    @call-page="callPage",
                  )
                  // datatable ends
          // users list ends
</template>

<script>
import VueCookies from "vue-cookies";
import { Toast } from "@/common/plugins/sweetalert";
import { TableScripts } from "@/common/global/scripts";
import { addStaticScripts } from "@/common/utils/addStatics";
import TalentService from "@/services/TalentService.js";
import BodyComponent from "@/layouts/BodyComponent";
export default {
  components: {
    BodyComponent,
  },
  data() {
    return {
      persons: {},
      token: "",
      filterDatas: [
        {
          name: "isBlocked",
          title: "بلاک شده",
          options: [
            { value: "false", title: "خیر" },
            { value: "true", title: "بله" },
          ],
        },
        {
          name: "isActive",
          title: "فعال",
          options: [
            { value: "true", title: "بله" },
            { value: "false", title: "خیر" },
          ],
        },
        {
          name: "isApproved",
          title: "تایید شده",
          options: [
            { value: "true", title: "بله" },
            { value: "false", title: "خیر" },
          ],
        },
      ],
      formData: {},
      pagination: { currentPage: 1 },
    };
  },
  methods: {
    // call needed function to apply filter
    submitFilter(submitEvent) {
      this.getFormData(submitEvent);
      this.getTalentsWithFilter();
    },
    // fill or update form data
    getFormData(submitEvent) {
      const data = submitEvent.target.elements;
      this.formData = {
        isBlocked: JSON.parse(data.isBlocked.value),
        isActive: JSON.parse(data.isActive.value),
        isApproved: JSON.parse(data.isApproved.value),
      };
    },
    getTalentsWithFilter() {
      TalentService.getAllTalentsByFilter(this.token, this.formData)
        .then(response => (this.persons = response.data))
        .catch(error => {
          Toast("error", "مشکلی در اعامل فیلتر رخ داد.");
          console.log(`there was an error: ${error}`);
        });
    },
    callPage(page) {
      this.token = VueCookies.get("adminToken");
      TalentService.getAllTalents(this.token, page)
        .then(response => {
          this.persons = response.data;
          this.pagination = response.data.meta;
        })
        .catch(error => {
          Toast("error", "مشکلی در دریافت اطلاعات لیست رخ داد.");
          console.log(`there was an error: ${error}`);
        });
    },
  },
  created() {
    this.callPage(this.pagination.currentPage);
  },
  mounted() {
    addStaticScripts(TableScripts);
  },
};
</script>

<style lang="scss" scoped>
@import "/app-assets/vendors/data-tables/css/jquery.dataTables.min.css";
@import "/app-assets/vendors/data-tables/extensions/responsive/css/responsive.dataTables.min.css";
@import "/app-assets/css-rtl/pages/page-users.min.css";
</style>
