<template lang="pug">
BodyComponent
  #main
    .row
      C-Breadcrump(
        :title="breadcrumpName",
        start="خانه",
        :startPath="{ name: 'home-page' }",
        middle="تلنت‌ها",
        :middlePath="{ name: 'all-talents' }",
        :last="breadcrumpName"
      )
      .col.s12
        .container
          // users edit start
          .section.users-edit
            .card
              .card-content
                .row
                  #account.col.s12.active
                    // users edit account form start
                    form#accountForm(novalidate="novalidate", @submit.prevent="updateTalent")
                      .row
                        // users edit media object start
                        .media.display-flex.align-items-center.mb-2
                          router-link.mr-2(:to="{ name: 'show-talent', props: { id: talent.id } }")
                            img.z-depth-4.circle(
                              :src="[talent.image ? talent.image : '/app-assets/images/avatar/guest.png']",
                              alt="users avatar",
                              height="150",
                              width="150"
                            )
                          .media-body
                            h5.media-heading.mt-0 {{ talent.name }}
                            .user-edit-btns.display-flex
                              button.btn-small.indigo(type="submit") تغییر دادن
                              a.btn-small.btn-light-pink(@click="getTalentData") تنظیم مجدد
                        // users edit media object ends
                        .col.s12.m6
                          .row
                            .col.s12.input-field
                              input#name.validate(
                                name="name",
                                type="text",
                                :value="talent.name",
                                data-error=".errorTxt2"
                              )
                              label.active(for="name") نام
                              small.errorTxt2

                            .col.s12.input-field
                              input#email.validate(
                                name="email",
                                type="email",
                                :value="talent.email",
                                data-error=".errorTxt3"
                              )
                              label.active(for="email") ایمیل
                              small.errorTxt3

                            .col.s12.input-field
                              input#book_price.validate(
                                name="book_price",
                                type="number",
                                :value="talent.book_price",
                                data-error=".errorTxt3"
                              )
                              label.active(for="book_price") قیمت ویدیو
                              small.errorTxt3

                            .col.s12.input-field
                              select#gender.validate(name="gender", tabindex="-1")
                                option(value="MALE", :selected="talent.gender === 'MALE'") MALE
                                option(value="FEMALE", :selected="talent.gender === 'FEMALE'") FEMALE
                              label(for="gender") جنسیت
                              small.errorTxt3

                            .col.s12.input-field
                              input#password.validate(name="password", type="text", data-error=".errorTxt3")
                              label.active(for="password") رمز عبور
                              small.errorTxt3

                        .col.s12.m6
                          .row
                            .col.s12.input-field
                              input#popularity.validate(
                                name="popularity",
                                type="text",
                                :value="talent.popularity",
                                data-error=".errorTxt3"
                              )
                              label.active(for="blocked_cash") محبوبیت
                              small.errorTxt3

                            .col.s12.input-field
                              textarea#short_description.materialize-textarea.validate(
                                v-model="talent.short_description",
                                style="height: 100px;"
                              )
                                | {{ talent.short_description }}
                              label(for="short_description") توضیحات کوتاه

                            .col.s12.input-field
                              textarea#description.materialize-textarea.validate(
                                v-model="talent.description",
                                style="height: 153px;"
                              )
                                | {{ talent.description }}
                              label(for="description") توضیحات

                        // START: STATUS CHANGE
                        .col.s12
                          table.mt-1
                            thead
                              tr
                                th
                                th بلاک شده
                                th فعال
                                th تایید شده
                            tbody
                              tr
                                th وضعیت

                                //- بلاک شده
                                td
                                  label
                                    input(
                                      :value="talent.isBlocked",
                                      @change="toggleValue($event)",
                                      name="isBlocked",
                                      type="checkbox",
                                      :checked="talent.isBlocked"
                                    )
                                    span

                                //- فعال
                                td
                                  label
                                    input(
                                      :value="talent.isActive",
                                      @change="toggleValue($event)",
                                      name="isActive",
                                      type="checkbox",
                                      :checked="talent.isActive"
                                    )
                                    span

                                //- تایید شده
                                td
                                  label
                                    input(
                                      :value="talent.isApproved",
                                      @change="toggleValue($event)",
                                      name="isApproved",
                                      type="checkbox",
                                      :checked="talent.isApproved"
                                    )
                                    span
                        // END: STATUS CHANGE

                        .col.s12.display-flex.justify-content-end.mt-3
                          button.btn.indigo.iransans(type="submit")
                            | ذخیره تغییرات
                          button.btn.btn-light.iransans(type="button", @click="this.$router.go(-1)") لغو
                    // users edit account form ends
          // users edit ends
</template>

<script>
import { Toast } from "@/common/plugins/sweetalert";
import VueCookies from "vue-cookies";
import BodyComponent from "@/layouts/BodyComponent";
import TalentService from "@/services/TalentService.js";
export default {
  props: {
    id: { type: String },
  },
  components: {
    BodyComponent,
  },
  data() {
    return {
      token: "",
      talent: {},
      formData: {},
      breadcrumpName: "",
    };
  },
  methods: {
    // call needed function to update
    updateTalent(submitEvent) {
      this.getFormData(submitEvent);
      this.updateTalentRequest();
    },
    // fill or update form data
    getFormData(submitEvent) {
      const data = submitEvent.target.elements;
      this.formData = {
        name: data.name.value,
        email: data.email.value,
        gender: data.gender.value,
        password: data.password.value,
        popularity: data.popularity.value,
        book_price: Number(data.book_price.value),
        short_description: data.short_description.value,
        description: data.description.value,
        isBlocked: JSON.parse(data.isBlocked.value),
        isActive: JSON.parse(data.isActive.value),
        isApproved: JSON.parse(data.isApproved.value),
      };
      if (!this.formData.password.trim()) {
        delete this.formData.password;
      }
    },
    // toggle input checkbox value as boolean
    toggleValue: e => {
      let targetValue = JSON.parse(e.target.value);
      e.target.value = !targetValue;
    },
    // update patch request
    updateTalentRequest() {
      TalentService.updateTalent(this.token, this.id, this.formData)
        .then(response => {
          this.talent = response.data;
          this.breadcrumpName = `ویرایش ${this.talent.name}`;
          Toast("success", "به درستی آپدیت شد.");
        })
        .catch(error => {
          Toast("error", "مشکلی در آپدیت رخ داد.");
          console.log(`there was an error: ${error}`);
        });
    },
    // get data by id
    getTalentData() {
      this.token = VueCookies.get("adminToken");
      TalentService.getTalentById(this.token, this.id)
        .then(response => {
          this.talent = response.data;
          this.breadcrumpName = `ویرایش ${this.talent.name}`;
        })
        .catch(() => this.$router.go(-1));
    },
  },
  created() {
    this.getTalentData();
  },
};
</script>

<style lang="scss" scoped>
@import "/app-assets/vendors/select2/select2.min.css";
@import "/app-assets/vendors/select2/select2-materialize.css";
</style>
