<template lang="pug">
li.bold(:class="{ active: isActive }")
  a.collapsible-header.waves-effect.waves-cyan(href="Javascript:void(0)")
    i.material-icons {{ icon }}
    span.menu-title(:data-i18n="translateKey") {{ title }}
    span.badge.badge.pill.red.float-right.mr-10(v-if="notifCount && notifCount != 0") {{ notifCount }}
  .collapsible-body
    ul.collapsible.collapsible-sub(data-collapsible="accordion")
      slot
</template>

<script>
export default {
  props: {
    title: { type: String, default: "Drop Down Title Not Found" },
    translateKey: { type: String },
    isActive: { type: Boolean, default: false },
    icon: { type: String, default: "info_outline" },
    notifCount: { type: [Number, String], default: null },
  },
};
</script>
