<template lang="pug">
.responsive-table
  #users-list-datatable_wrapper.dataTables_wrapper.no-footer
    table#users-list-datatable.table.dataTable.no-footer.dtr-inline(
      role="grid",
      aria-describedby="users-list-datatable_info",
      style="width: 1560px"
    )
      thead
        tr(role="row")
          th.sorting_asc(rowspan="1", colspan="1", style="width: 32px", aria-label="")
          th(
            v-for="(header, headerKey) in headers",
            :key="headerKey",
            tabindex="0",
            aria-controls="users-list-datatable",
            rowspan="1",
            colspan="1",
            style="width: 112px",
            aria-label=`${header}: activate to sort column ascending`
          ) {{ header }}
          th.sorting_disabled(
            v-if="editComponent",
            rowspan="1",
            colspan="1",
            style="width: 129px",
            aria-label="ویرایش"
          ) ویرایش
          th.sorting_disabled(v-if="showComponent", rowspan="1", colspan="1", style="width: 72px", aria-label="نمایش") نمایش
      tbody
        tr(role="row", v-for="(data, index) in AllData.items", :class="[index % 2 ? 'even' : 'odd']", :key="index")
          td.sorting_1(tabindex="0")
          td(v-if="id") {{ data.id }}
          td {{ data.first || data.last ? `${data.first} ${data.last}` : data.name }}
          td {{ data.mobile }}
          td(v-if="data.popularity") {{ data.popularity || 'خالی' }}

          td(v-if="data.isBlocked")
            span.chip.red.lighten-5
              span.red-text بلاک شده
          td(v-else)
            span.chip.green.lighten-5
              span.green-text فعال

          td(v-if="data.isActive")
            span.chip.green.lighten-5
              span.green-text فعال
          td(v-else)
            span.chip.red.lighten-5
              span.red-text غیرفعال

          td(v-if="data.isApproved")
            span.chip.green.lighten-5
              span.green-text تایید شده
          td(v-else)
            span.chip.red.lighten-5
              span.red-text تایید نشده
          td(v-if="editComponent")
            router-link(:to="{ name: editComponent, params: { id: data.id } }")
              i.material-icons edit
          td(v-if="showComponent")
            router-link(:to="{ name: showComponent, params: { id: data.id } }")
              i.material-icons remove_red_eye

    #users-list-datatable_info.dataTables_info(role="status", aria-live="polite") نمایش {{ pagination.currentPage * pagination.itemsPerPage - pagination.itemsPerPage + 1 }} تا {{ pagination.currentPage * pagination.itemsPerPage }} از {{ pagination.totalItems }} داده
    #users-list-datatable_paginate.dataTables_paginate.paging_simple_numbers
      a#users-list-datatable_previous.paginate_button.previous.disabled(
        aria-controls="users-list-datatable",
        data-dt-idx="0",
        tabindex="-1",
        @click="firstPage"
      ) اولین
      span
        a.paginate_button(
          v-for="(page, pageIndex) in pagination.totalPages",
          :class="{ current: pagination.currentPage - 1 === pageIndex }",
          v-show="pagination.currentPage > pageIndex - 5",
          :key="pageIndex",
          aria-controls="users-list-datatable",
          data-dt-idx="1",
          tabindex="0",
          @click="callPage"
        ) {{ pageIndex + 1 }}
      a#users-list-datatable_next.paginate_button.next(
        aria-controls="users-list-datatable",
        data-dt-idx="5",
        tabindex="0",
        @click="lastPage"
      ) آخرین
</template>

<script>
import { ToEnlishNumber } from "@/common/utils/helpers";
export default {
  props: {
    id: { type: Boolean, default: false },
    AllData: { type: [Array, Object] },
    editComponent: { type: String },
    showComponent: { type: String },
    headers: { type: Array },
    pagination: { type: Object }, // contain { currentPage, itemCount, itemsPerPage, totalItems, totalPages }
  },
  data() {
    return {
      limit: 10,
    };
  },
  methods: {
    firstPage() {
      this.$emit("call-page", 1);
    },
    callPage(e) {
      this.$emit("call-page", ToEnlishNumber(e.path[0].innerText));
    },
    lastPage() {
      this.$emit("call-page", this.pagination.totalPages);
    },
  },
};
</script>
