import AllAdmins from "@/views/admins/AllAdmins.vue";
import EditAdmin from "@/views/admins/EditAdmin.vue";
import ShowAdmin from "@/views/admins/ShowAdmin.vue";
import CreateAdmin from "@/views/admins/CreateAdmin.vue";

export const adminRoutes = [
  { path: "/admins", name: "all-admins", component: AllAdmins },
  { path: "/admins/create", name: "create-admin", component: CreateAdmin },
  { path: "/admins/:id", name: "show-admin", component: ShowAdmin, props: true },
  { path: "/admins/:id/edit", name: "edit-admin", component: EditAdmin, props: true },
];
