import AllTalents from "@/views/talents/AllTalents.vue";
import EditTalent from "@/views/talents/EditTalent.vue";
import ShowTalent from "@/views/talents/ShowTalent.vue";
import CreateTalent from "@/views/talents/CreateTalent.vue";

export const talentRoutes = [
  { path: "/talents", name: "all-talents", component: AllTalents },
  { path: "/talents/create", name: "create-talent", component: CreateTalent },
  { path: "/talents/:id", name: "show-talent", component: ShowTalent, props: true },
  { path: "/talents/:id/edit", name: "edit-talent", component: EditTalent, props: true },
];
